.ingred_adder {
    width: 9vw;
    display: flex;
    flex-grow: 2;
    overflow: visible;

    &__tabs {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        background-color: unset;
        justify-content: space-around; }
    &__tab {
        cursor: pointer;
        padding-right: 10px;
        line-height: 0.5;
        border-radius: 10px 0 0 10px;
        margin: 0;
        height: 18vh;
        font-size: 4vh;
        writing-mode: vertical-rl;
        text-orientation: mixed;
        background-color: #ccc;
        &--active {
            background-color: #fff; } }


    &__buttons {
        width: 8vw;
        flex-grow: 8;
        background-color: #fff; } }
